import { Component, OnDestroy, Input } from '@angular/core';
// import { NgFor } from '@angular/common';
import { SlideComponent } from '../slide/slide.component';

export enum Direction { UNKNOWN, NEXT, PREV }

@Component({
    selector: 'carousel',
    template: `
    <div  class="carousel slide" style='background-color:black'>
    <ol class="carousel-indicators" [hidden]="slides.length <= 1" style="display:none;">
    
         <li *ngFor="let slidez of slides" [class.active]="slidez.active === true" (click)="select(slidez)"></li>
       
      </ol>
      <div class="carousel-inner"><ng-content></ng-content></div>
                  <a id="leftarrow" class="left carousel-control" style="height:140px;display:none;" (click)="prev()" [hidden]="slides.length<2" >
                  <span class="glyphicon glyphicon-chevron-left"></span>
                  </a>
                  <a  id="rightarrow" class="right carousel-control" style="height:140px;display:none;" (click)="next()" [hidden]="slides.length<2">
                  <span class="glyphicon glyphicon-chevron-right"></span>
                 </a>
    </div>
  `
})
export class CarouselComponent implements OnDestroy {

    @Input() public noWrap: boolean;
    @Input() public noPause: boolean;
    @Input() public noTransition: boolean;

    private slides: Array<SlideComponent> = [];
    private currentInterval: any;

    private destroyed: boolean = false;
    private currentSlide: SlideComponent;

    public ngOnDestroy() {
        this.destroyed = true;
    }

    public select(nextSlide: SlideComponent, direction: Direction = Direction.UNKNOWN) {
        let nextIndex = nextSlide.index;
        if (direction === Direction.UNKNOWN) {
            direction = nextIndex > this.getCurrentIndex() ? Direction.NEXT : Direction.PREV;
        }

        // Prevent this user-triggered transition from occurring if there is already one in progress
        if (nextSlide && nextSlide !== this.currentSlide) {
            this.goNext(nextSlide, direction);
        }
    }

    private goNext(slide: SlideComponent, direction: Direction) {
        if (this.destroyed) {
            return;
        }

        slide.direction = direction;
        slide.active = true;

        if (this.currentSlide) {
            this.currentSlide.direction = direction;
            this.currentSlide.active = false;
        }

        this.currentSlide = slide;
    }

    private getSlideByIndex(index: number) {
        let len = this.slides.length;
        for (let i = 0; i < len; ++i) {
            if (this.slides[i].index === index) {
                return this.slides[i];
            }
        }
    }

    private getCurrentIndex() {
        return !this.currentSlide ? 0 : this.currentSlide.index;
    }

    private next() {
        let newIndex = (this.getCurrentIndex() + 1) % this.slides.length;

        if (newIndex === 0 && this.noWrap) {

            return;
        }

        return this.select(this.getSlideByIndex(newIndex), Direction.NEXT);
    }

    private prev() {
        let newIndex = this.getCurrentIndex() - 1 < 0 ? this.slides.length - 1 : this.getCurrentIndex() - 1;

        if (this.noWrap && newIndex === this.slides.length - 1) {
            return;
        }

        return this.select(this.getSlideByIndex(newIndex), Direction.PREV);
    }

    public addSlide(slide: SlideComponent) {
        slide.index = this.slides.length;
        this.slides.push(slide);
        if (this.slides.length === 1 || slide.active) {
            this.select(this.slides[this.slides.length - 1]);

        } else {
            slide.active = false;
        }
    }

    displayArrow(index, length) {
        console.log('enter1');
        if (length > 1) {
            var el = document.getElementsByClassName('left carousel-control')[index] as HTMLElement;
            el.style.display = 'block';
            var rarr = document.getElementsByClassName('right carousel-control')[index] as HTMLElement;
            rarr.style.display = 'block';
            var ind = document.getElementsByClassName('carousel-indicators')[index] as HTMLElement;
            ind.style.display = 'block';
        }
    }

    hideArrow(index) {
        var el = document.getElementsByClassName('left carousel-control')[index] as HTMLElement;
        el.style.display = 'none';
        var rarr = document.getElementsByClassName('right carousel-control')[index] as HTMLElement;
        rarr.style.display = 'none';

        var ind = document.getElementsByClassName('carousel-indicators')[index] as HTMLElement;
        ind.style.display = 'none';
    }

}




