import { GoogleMapsAPIWrapper } from '@agm/core';
import { Directive, Input, Output } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
// const decodePolyline = require('decode-google-map-polyline');
import * as decodePolyline from 'decode-google-map-polyline';

declare var google: any;

@Directive({
  selector: 'appDirectionsMap'
})


export class DirectionsMapDirective {

  @Input() origin: any;
  @Input() destination: any;
  @Input() originPlaceId: any;
  @Input() destinationPlaceId: any;
  @Input() waypoints: any;
  @Input() directionsDisplay: any;
  @Input() estimatedTime: any;
  @Input() estimatedDistance: any;
  way = [];
  p;
  polyline;
  endicon = '/assets/images/end.png';
  starticon = '/assets/images/start1.png';
  constructor(private gmapsApi: GoogleMapsAPIWrapper, private activatedRoute: ActivatedRoute) {
    this.activatedRoute.params.subscribe((params: Params) => {
      this.polyline = params['polyline'];

      //       console.log(this.user);
    });
    //var polyline = 'q{xlEqzc}Ls{tAyeXimrBcny@';
    console.log(decodePolyline(this.polyline));
    this.p = decodePolyline(this.polyline);
    var a = [];
    for (var i = 0; i < this.p.length; i++) {
      this.way.push({ location: { lat: this.p[i]['lat'], lng: this.p[i]['lng'] }, stopover: true });

    }
    /* this.way=[
  
    {location:{lat:30.18787,lng:71.46606},
         stopover: true 
   },
  
  
   ];*/

  }
  updateDirections() {
    this.gmapsApi.getNativeMap().then(map => {


      var directionsService = new google.maps.DirectionsService;
      var me = this;

      this.directionsDisplay.setMap(map);
      this.directionsDisplay.setOptions({
        polylineOptions: {
          strokeWeight: 8,
          strokeOpacity: 0.7,
          strokeColor: '#00468c'
        },
        suppressMarkers: true
      });
      this.directionsDisplay.setDirections({ routes: [] });
      directionsService.route({
        // origin: {placeId : 'ChIJL3KReNC_3zgRtgLbO1xRWWA' },
        origin: { location: { lat: this.p[0]['lat'], lng: this.p[0]['lng'] } },
        waypoints: this.way,
        optimizeWaypoints: true,
        //destination: {placeId : 'ChIJ2QeB5YMEGTkRYiR-zGy-OsI'},
        destination: { location: { lat: this.p[this.p.length - 1]['lat'], lng: this.p[this.p.length - 1]['lng'] } },
        avoidHighways: true,
        travelMode: google.maps.DirectionsTravelMode.DRIVING
        //travelMode: 'DRIVING'
      }, function (response: any, status: any) {
        if (status === 'OK') {
          me.directionsDisplay.setDirections(response);
          map.setZoom(30);

          me.addMarker(me.p[0]['lat'], me.p[0]['lng'], me.starticon);
          me.addMarker(me.p[me.p.length - 1]['lat'], me.p[me.p.length - 1]['lng'], me.endicon);
          //console.log(me.getcomputeDistance (latLngA, latLngB));
          var point = response.routes[0].legs[0];
          me.estimatedTime = point.duration.text;
          me.estimatedDistance = point.distance.text;
          console.log(me.estimatedTime);
          console.log('Estimated travel time: ' + point.duration.text + ' (' + point.distance.text + ')');

        } else {
          console.log('Directions request failed due to ' + status);
        }
      });
    });

  }
  private addMarker(lat, lng, icon) {
    // this.gmapsApi.createMarker({ position: { lat: lat - .001, lng: lng - .001 }, icon: icon });
  }

  private getcomputeDistance(latLngA: any, latLngB: any) {
    return (google.maps.geometry.spherical.computeDistanceBetween(latLngA, latLngB) / 1000).toFixed(2);
  }

}



