import { Injectable } from '@angular/core';

import { ParseService } from './parse.service';
import { Util } from './util';

@Injectable()
export class LiveQueryService {

	parse;
	constructor(parseService: ParseService) {
		this.parse = parseService.sdk();
	}

	public getLiveQueryClient() {
		let LiveQueryClient = this.parse.LiveQueryClient;
		let client = new LiveQueryClient({
			applicationId: Util.PARSE_APP_ID,
			serverURL: Util.PARSE_LIVE_QUERY_URL,
			javascriptKey: '',
			masterKey: ''
		});

		return client;
	}

}



