import { Component, OnInit , ViewChild } from '@angular/core';
import { ParseService } from '../parse.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { SlideComponent } from '../slide/slide.component';
import { CarouselComponent } from '../carousel/carousel.component';
import * as _ from 'underscore';
let imagePath = '/assets/images/p2.png';
@Component({
  selector: 'app-discover-journey',
  templateUrl: './discover-journey.component.html',
  styleUrls: ['./discover-journey.component.css']
})
export class DiscoverJourneyComponent implements OnInit {

  parse;
  zindex = 900;
  journey_id;
  journeyData = [];
  messages=[];
  @ViewChild(CarouselComponent)
  private carouselComponent: CarouselComponent;
  constructor(
   
    //private _elementRef: ElementRef,
    parse: ParseService, private router: Router, private activatedRoute: ActivatedRoute
  ) {

    this.activatedRoute.params.subscribe((params: Params) => {
      this.journey_id = params['id'];
      
    });
   
    this.parse = parse.sdk();
    //var Media = this.parse.Object.extend('Media');
    //var media = new Media();
    var Journey = this.parse.Object.extend('Journeys');
    //var qu=new this.parse.Query(journey);
    //  qu.equalTo('objectId',this.journey_id);
    var journey = new Journey();
    journey.id = this.journey_id;

    console.log(journey);
    var query = new this.parse.Query(Journey);
    query.equalTo("objectId", this.journey_id);
    query.include("createdBy");
    query.find(
      journey => {
        for (var j = 0; j < journey.length; j++) {
          var objt = journey[j];
          var videoUrl;
          var length;
          var dis = Math.floor(objt.get('distance'));
          if (objt.get('videoUrl')) {
            videoUrl = objt.get('videoUrl');
            length = 2;
          }
          else { videoUrl = false; length = 1; }
          var path;
          if (objt.get('createdBy').get('profilePic'))
              path = objt.get('createdBy').get('profilePic').url();
          else path = imagePath;
          var uname = objt.get('createdBy').get('name');
          var ms = new Date().getTime() - new Date(objt.get('createdAt')).getTime();
          var time = this.timeConversion(ms);
          var millisec = new Date(objt.get('endDate')).getTime() - new Date(objt.get('startDate')).getTime();
          var timejourneytake = this.timeConversion(millisec);
          this.journeyData.push({
                              id: objt.id, description: objt.get('description'),posttime:time,username:uname,pimage:path,
                              polyline: objt.get('polylines'), numMedia: objt.get('numOfMedia'),
                              distance: dis, title: objt.get('name'), view: objt.get('views'),numLikes:objt.get('likesCounter'),
                              video: videoUrl, length: length,startLocation:objt.get('startLocation'),
                              endLocation:objt.get('endLocation'),time:timejourneytake
          });
        }

        console.log(this.journeyData);
      }
    );
    this.getComment(journey)
    
  }

  ngOnInit() {
  }
  getComment(journey){
    var Comment = this.parse.Object.extend('CommentJourney');
    var queryC = new this.parse.Query(Comment);
    var messages1 = [];
    queryC.equalTo("journey", journey);

   
      var u_id;
      // 
      queryC.ascending('createdAt');
      queryC.include('user');
      queryC.find().then(
        comment => {
          // Do stuff
          var promise = this.parse.Promise.as();
          _.each(comment, (resultObj) => {

            promise = promise.then(abc => {
              var ob = <any>resultObj;

              console.log(ob.get('text'));
              if (!ob.get('user').get('profilePic')) {
                this.messages.push({ from: ob.get('user').get('username'), image: imagePath, message: ob.get('text') });
                console.log(this.messages);
              }
              else {
                this.messages.push({ from: ob.get('user').get('username'), image: ob.get('user').get('profilePic').url(), message: ob.get('text') });
              }
            });
          });
          return promise;
        });
     // this.messages[index] = messages1;

  }
  private displayArrow(index, length) {
    console.log(index);
    this.carouselComponent.displayArrow(index, length);  //calling displayArrow func in carouselComponent to show arrow on card
  }

  private hideArrow(index) {
    console.log('leave');
    this.carouselComponent.hideArrow(index); //calling hideArrow func in carouselComponent to hide arrow on card
  }

  private playPauseVideo(index) {
    console.log('play');
    var el = document.getElementsByClassName('video')[index] as HTMLVideoElement;
    var em = document.getElementsByClassName('playpause')[index] as HTMLElement;
    if (el.paused) {
      console.log('play2');
      el.play()
      em.style.opacity = '0';
    }
    else {
      el.pause();
      em.style.opacity = '1';
    }
  }
  timeConversion(millisec) {

    var seconds = <any>(millisec / 1000).toFixed();

    var minutes = <any>(millisec / (1000 * 60)).toFixed();

    var hours = <any>(millisec / (1000 * 60 * 60)).toFixed();

    var days = <any>(millisec / (1000 * 60 * 60 * 24)).toFixed();

    if (seconds < 60) {
      return seconds + " Sec";
    } else if (minutes < 60) {
      return minutes + " Min";
    } else if (hours < 24) {
      return hours + " Hrs";
    } else {
      return days + " Days"
    }
  }
}



