import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { ChatComponent } from '../chat/chat.component';
import { HomeComponent } from '../home/home.component';
import { DynamicComponent } from '../dynamic/dynamic.component';
import { ParseService } from '../parse.service';
import { Util } from '../util';
let imagePath = '/assets/images/p2.png';

@Component({
    selector: 'app-following-list',
    templateUrl: './following-list.component.html',
    styleUrls: ['./following-list.component.css']
})
export class FollowingListComponent implements OnInit {
    
    total_popups = 0;
    parse;
    user;
    followinglist = [];
    following_id = [];
    searchedList = [];
    //arrays of popups ids
    popups = [];
    componentData = null;
    @Output()
    uploaded: EventEmitter<string> = new EventEmitter();

    constructor(parseService: ParseService) {
        this.parse = parseService.sdk();
        this.user = this.parse.User.current();

        var Connection = this.parse.Object.extend('Connection');
        var query2 = new this.parse.Query(Connection);
        var queryU = new this.parse.Query(this.parse.User);
        query2.equalTo("from", this.user);
        query2.include('to');
        query2.find().then(
            user => {
                for (var i = 0; i < user.length; i++) {
                    var obj = user[i];
                    if (obj.get('to').get('profilePic'))
                         this.followinglist.push({ username: obj.get('to').get('username'), image: obj.get('to').get('profilePic').url() });
                     else this.followinglist.push({ username: obj.get('to').get('username'), image: imagePath });
                    this.following_id.push(obj.get('to').id);
                }

            }).then(az => {
                queryU.containedIn('objectId', this.following_id);    //to get the name and profile image of user to display on card
                queryU.find().then(
                    user => {
                        this.searchedList = user;
                    });
            });
    }

    ngOnInit() {
    }

    

    showChatBox(chatname, index) {
        Util.selectedChat = null;
        console.log(this.searchedList[index]);
        Util.otherChatUser = this.searchedList[index];
        this.uploaded.emit(chatname);
    }

}



