import { Component, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser'
import { Router, ActivatedRoute, Params } from '@angular/router';
import { AuthenticationService, User } from '../authentication.service'
import { ParseService } from '../parse.service';
import * as _ from 'underscore';
import { Util } from '../util';
let imagePath = '/assets/images/p2.png';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css'],
  providers: [AuthenticationService],
})
export class ProfileComponent implements OnInit {

  comment: boolean = false;
  view: number = 1;
  name: string;
  htmltoadd: string;
  parse;
  followButton;
  listname;
  coverImg;
  profileImg;
  coverImgUser;
  profileImgUser;
  imagePath1 = '/assets/images/p2.png';
  imageCover = '/assets/images/backlogin.jpg';
  // the logged in user
  currentUser;

  // user who's profile is being viewed
  profileUser;
  profileUsername;
  profileUserId;

  messages = [];
  postData = [];
  _router: Router;

  constructor(private _service: AuthenticationService, parse: ParseService, router: Router, private activatedRoute: ActivatedRoute) {
    this._router = router;
    this.parse = parse.sdk();

    this.followButton = 'Follow';
    this.currentUser = this.parse.User.current();
  }

  ngOnInit() {
    this.activatedRoute.params.subscribe((params: Params) => {
      this.profileUsername = params['username'];
      this.profileUserId = params['userId'];

      // if profile is of current user, set data on view immediately
      if (this.currentUser.id == this.profileUserId) {
        this.profileUser = this.currentUser;
      }
      else {
        this.fetchProfileUser();
      }

      this.fetchProfileUserPosts();

      this.updatePhotos();

      this.setFollowButtonState();
    });
  }


  fetchProfileUser() {
    // TODO show spinner until loaded
    var query = new this.parse.Query(this.parse.User);
    query.get(this.profileUserId, {
      success: (user) => {
        this.profileUser = user;
      },
      error: (user, error) => {
        console.error(error);
      }
    });
  }

  /**
   * updates the image paths on user profile 
   */
  updatePhotos() {
    // Updating current user's attributes
    this.currentUser.fetch({
      success: user => {
        this.currentUser = user;


        if (user.get('coverPic')) {
          this.coverImgUser = user.get('coverPic').url();


        }
        else {
        this.coverImgUser = this.imageCover;
          console.log('cover');
        }

        if (user.get('profilePic'))
          this.profileImgUser = user.get('profilePic').url();

        else {
        this.profileImgUser = this.imagePath1;
          console.log('else');
        }
      },
      error: (user, err) => {
        console.error(err);
      }
    });
  }

  // related to followButton
  STATE_FOLLOWING = 'following';
  STATE_PENDING = 'pending';
  STATE_NONE = 'none';

  // sets the icon inside
  VALUE_FOLLOWING = 0;
  VALUE_FOLLOWING_HOVER = 1; // actually a prompt to cancel the connection
  VALUE_PENDING = 2;
  VALUE_PENDING_HOVER = 3; // actually a prompt to cancel the request
  VALUE_NONE = 4;

  followButtonState = this.STATE_NONE;
  followButtonValue = this.VALUE_NONE;
  /**
   * sets the text on Follow/Unfollow button according to connection
   */
  setFollowButtonState() {
    console.log('settin follow button state');

    // construct the Pointer to profileUser
    var profileUser = this.parse.User.createWithoutData(this.profileUserId);

    var Connection = new this.parse.Object('Connection');
    var query = new this.parse.Query(Connection);
    query.equalTo("from", this.currentUser);
    query.equalTo("to", profileUser);
    query.find({
      // ideally only one connection must exist in this direction, so one should be fetched
      success: conn => {
        if (conn.length === 0) {
          // connection doesn't exist
          this.followButtonState = this.STATE_NONE;
          this.followButtonValue = this.VALUE_NONE;
          console.log("setted state of follow to: ", this.followButton);
        }
        else {
          // the currentUser is already following this user, so
          // this.followButton = 'Unfollow';
          this.followButtonState = this.STATE_FOLLOWING;
          this.followButtonValue = this.VALUE_FOLLOWING;
          console.log("setted state of follow to: ", this.followButton);
        }
      },
      error: err => {
        console.error(err);
      }
    });
  }

  hover() {
    console.log('hover');
    if (this.followButtonState == this.STATE_FOLLOWING) {
      this.followButtonValue = this.VALUE_FOLLOWING_HOVER;
    }
    else if (this.followButtonState == this.STATE_PENDING) {
      this.followButtonValue = this.VALUE_PENDING_HOVER;
    }
    else if (this.followButtonState == this.STATE_NONE) {
      this.followButtonValue = this.VALUE_NONE;
    }
  }

  unhover() {
    console.log('unhover');

    if (this.followButtonState == this.STATE_FOLLOWING) {
      this.followButtonValue = this.VALUE_FOLLOWING;
    }
    else if (this.followButtonState == this.STATE_PENDING) {
      this.followButtonValue = this.VALUE_PENDING;
    }
    else if (this.followButtonState == this.STATE_NONE) {
      this.followButtonValue = this.VALUE_NONE;
    }
  }

  fetchProfileUserPosts() {
    var Post = this.parse.Object.extend('Post');
    var query = new this.parse.Query(Post);
    query.equalTo("userId", this.profileUserId);
    query.descending('createdAt');
    query.find({
      success: posts => {
        for (var i = 0; i < posts.length; i++) {
          var obj = posts[i];
          var img = obj.get('media');
          this.postData.push({ name: this.profileUser, profileImg: this.profileImgUser, title: obj.get('title'), description: obj.get('body'), images: img });
          console.log(obj.get('media'));
        }
      },
      error: (posts, err) => { console.error(err); }
    });
  }

  /**
   * Takes to the Page that shows create post form
   */
  gotoCreate() {
    this._router.navigate(['create']);
  }

  addComment(index, trackBy) {
    var e = document.getElementsByClassName('comment_box')[index] as HTMLElement;
    //console.log(e);
    var messages1 = [];
    this.messages = [];
    var Comment = this.parse.Object.extend('Comment');
    var query = new this.parse.Query(Comment);
    var queryU = new this.parse.Query(this.parse.User);
    if (e.style.display === "none") {
      e.style.display = "block";
      var u_id;
      query.equalTo("post_id", trackBy);  // 
      query.ascending('createdAt');
      query.find().then(
        comment => {
          // Do stuff
          var promise = this.parse.Promise.as();
          _.each(comment, (resultObj) => {
            var ob = <any>resultObj;
            console.log('h1', ob.get('userId'));
            queryU.equalTo("objectId", ob.get('userId'));
            //adList.push(ad);
            return queryU.first().then((results) => {
              // adList.push(5);
              console.log(ob.get('text'));
              this.messages.push({ from: results.get('username'), message: ob.get('text') });
              console.log(this.messages);
            });
          });
          return promise;
        });
    }
    else { e.style.display = 'none'; }
  }

  uploadProfileImg(event) {
    var el = document.getElementById('profile');
    var imgsrc = document.getElementById('img') as HTMLImageElement;
    var currentUser = this.parse.User.current();
    var profileImage = new this.parse.Parse.File('profile', event.target.files[0]);
    currentUser.set('profilePic', profileImage);
    currentUser.save({
      success: (user) => {
        currentUser = user;
        console.log(profileImage.url());
        console.log(currentUser.coverImage);
        console.log('saved profile img');
      },
      error: (user, error) => {
        console.log("upload failed. please try again!", error);
      }
    });
    if (event.target.files[0]) {
      var reader = new FileReader();
      console.log(reader);
      reader.onload = ((e: any) => {
        imgsrc.src = e.target.result;             //display image when user upload it
        this.profileImgUser = e.target.result;
        Util.profileImg = this.profileImgUser;
        console.log('em.src');
      });
      reader.readAsDataURL(event.target.files[0]);
      reader.onerror = error => {
        console.log('Error: ', error);
      };
    }
  }

  uploadCoverImg(event) {
    var el = document.getElementById('cover');
    var imgsrc = document.getElementById('cov') as HTMLImageElement;
    var file = new this.parse.Parse.File('cover', event.target.files[0]);

    this.currentUser.set('coverPic', file);
    this.currentUser.save({
      success: (user) => {
        this.currentUser = user;
        console.log('Uploaded coverImg: ', this.currentUser.coverImage);
      },
      error: (user, err) => {
        console.error(err);
      }
    });
    if (event.target.files[0]) {
      var reader = new FileReader();
      console.log(reader);
      reader.onload = ((e: any) => {
        //display image when user upload it

        this.coverImgUser = e.target.result;
        Util.coverImg = this.coverImgUser;
        console.log('em.src');
      });
      reader.readAsDataURL(event.target.files[0]);
      reader.onerror = error => {
        console.log('Error: ', error);
      };
    }
  }

  /**
   * Gets called when "Follow" button is clicked
   */
  follow() {
    // make pointer to profile user
    var profileUser = this.parse.User.createWithoutData(this.profileUserId);

    // save as Request object
    var Request = this.parse.Object.extend("Request");
    var req = new Request();
    req.set("to", profileUser)
      .set("from", this.currentUser)
      .set("state", "sent")
      .save();
  }

  saveComment(event, post_id) {             //save comment
    if (event.keyCode == 13) {      //when enter key is pressed
      var Comment = this.parse.Object.extend('Comment');
      var comment = new Comment();
      comment.set('userId', this.parse.User.current().id);
      comment.set('post_id', post_id);
      comment.set('text', event.target.value);
      // for now
      comment.set('type', 'plain_comment');
      comment.save({
        success: (comment) => {
          console.log('Comment Saved.');
        },
        error: function (gameScore, error) {
          console.error(error);
        }
      });
      event.target.value = "";
    }
  }

  /**
   * Set object id to each post
   * @param index 
   * @param item 
   */
  identity(index, item) {
    return item.id;
  }

}


