import { Component, NgModule, NgZone, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormControl, FormsModule, ReactiveFormsModule } from "@angular/forms";
import { BrowserModule } from "@angular/platform-browser";
import { AgmCoreModule, MapsAPILoader, GoogleMapsAPIWrapper } from '@agm/core';
import { DirectionsMapDirective } from '../directions-map.directive';
import { ParseService } from '../parse.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { InboxComponent } from '../inbox/inbox.component';
import { CarouselComponent } from '../carousel/carousel.component';
// const decodePolyline = require('decode-google-map-polyline');
import * as decodePolyline from 'decode-google-map-polyline';

declare var google: any;
declare var jQuery: any;
let imagePath = '/assets/images/p2.png';

@Component({
  selector: 'app-journey-map',
  templateUrl: './journey-map.component.html',
  styleUrls: ['./journey-map.component.css'],
  providers: [GoogleMapsAPIWrapper]
})
export class JourneyMapComponent implements OnInit {

  public latitude: number;
  public longitude: number;
  public destinationInput: FormControl;
  public destinationOutput: FormControl;
  public zoom: number;
  public iconurl: string;
  public mapCustomStyles: any;
  public estimatedTime: any;
  public estimatedDistance: any;
  pllength;
  latlngBounds;
  parse;
  zindex = 900;
  journey_id;
  journeyData = [];
  deLatLng;
  image = '/assets/images/photo.png';
  video = '/assets/images/video.png';
  checkin = '/assets/images/checkin1.png';
  @ViewChild("scrollMe")
  private scrollContainer: ElementRef;

  @ViewChild(CarouselComponent)
  private carouselComponent: CarouselComponent;

  @ViewChild(DirectionsMapDirective) vc: DirectionsMapDirective;

  public origin: any; // its a example aleatory position
  public destination: any; // its a example aleatory position
  private polyline;
  private dePolylines = [];
  endicon = '/assets/images/flag.png';
  starticon = '/assets/images/start1.png';

  constructor(
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone,
    private gmapsApi: GoogleMapsAPIWrapper,
    private _elementRef: ElementRef,
    parse: ParseService, private router: Router, private activatedRoute: ActivatedRoute
  ) {

    this.activatedRoute.params.subscribe((params: Params) => {
      this.journey_id = params['id'];
      this.polyline = params['polyline'];
    });
    this.dePolylines = decodePolyline(this.polyline);
    this.latitude = this.dePolylines[0]['lat'];
    this.longitude = this.dePolylines[0]['lng'];
    this.pllength = this.dePolylines.length;
    console.log(this.dePolylines)
    this.parse = parse.sdk();
    var Media = this.parse.Object.extend('Media');
    var media = new Media();
    var Journey = this.parse.Object.extend('Journeys');
    //var qu=new this.parse.Query(journey);
    //  qu.equalTo('objectId',this.journey_id);
    var journey = new Journey();
    journey.id = this.journey_id;

    console.log(journey);
    var query = new this.parse.Query(Media);
    query.equalTo("belongsTo", journey);

    query.find(
      jo => {
        for (var j = 0; j < jo.length; j++) {
          var objt = jo[j];
          console.log(objt.get('content')[0]);
          var icon;
          if (objt.get('type') == 'IMAGE')
            icon = this.image;
          else if (objt.get('type') == 'VIDEO')
            icon = this.video;
          else if (objt.get('type') == 'CHECKIN')
            icon = this.checkin;
          this.journeyData.push({
            id: objt.id, text: objt.get('text'), location: objt.get('location'),
            content: objt.get('content'), type: objt.get('type'), zIndex: 900, icon: icon,
            startAddress:objt.get('startAddress'),finishAddress:objt.get('finishAddress')
            
          });
        }

        console.log(this.journeyData);
      }
    );
    var polyline = 'q{xlEqzc}Ls{tAyeXimrBcny@';
    console.log(decodePolyline(polyline));
    this.deLatLng = decodePolyline(polyline);
    var currentUser = this.parse.User.current();
  }

  ngOnInit() {
    this.mapsAPILoader.load().then(() => {
      this.latlngBounds = new window['google'].maps.LatLngBounds();
      this.dePolylines.forEach((location) => {
        this.latlngBounds.extend(new window['google'].maps.LatLng(location.lat, location.lng))
      })
    }
    )


  }



  /* if (this.vc.directionsDisplay === undefined) {
     this.mapsAPILoader.load().then(() => {
       this.vc.directionsDisplay = new google.maps.DirectionsRenderer;
     });

   }

   //Update the directions
   this.vc.updateDirections();
  // this.zoom = 12;*/


  private setupPlaceChangedListener(autocomplete: any, mode: any) {
    autocomplete.addListener("place_changed", () => {
      this.ngZone.run(() => {
        //get the place result
        // commented following line because 'google' was not gettting resolved
        // let place: google.maps.places.PlaceResult = autocomplete.getPlace();
        let place = autocomplete.getPlace();
        //verify result
        if (place.geometry === undefined) {
          return;
        }
        if (mode === 'ORG') {
          this.vc.origin = { longitude: place.geometry.location.lng(), latitude: place.geometry.location.lat() };
          this.vc.originPlaceId = place.place_id;
        } else {
          this.vc.destination = { longitude: place.geometry.location.lng(), latitude: place.geometry.location.lat() }; // its a example aleatory position
          this.vc.destinationPlaceId = place.place_id;
        }

        if (this.vc.directionsDisplay === undefined) {
          this.mapsAPILoader.load().then(() => {
            this.vc.directionsDisplay = new google.maps.DirectionsRenderer;
          });
        }

        //Update the directions
        this.vc.updateDirections();
        this.zoom = 12;
      });

    });

  }

  getDistanceAndDuration() {
    this.estimatedTime = this.vc.estimatedTime;
    this.estimatedDistance = this.vc.estimatedDistance;
  }

  scrollToBottom(): void {
    jQuery('html, body').animate({ scrollTop: jQuery(document).height() }, 3000);
  }
  private setPickUpLocation(place: any) {
    //verify result
    if (place.geometry === undefined || place.geometry === null) {
      return;
    }
    //set latitude, longitude and zoom
    this.latitude = place.geometry.location.lat();
    this.longitude = place.geometry.location.lng();
    this.zoom = 12;
  }

  private setCurrentPosition() {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.latitude = position.coords.latitude;
        this.longitude = position.coords.longitude;
        this.zoom = 12;
      });
    }
  }

  private getMapCusotmStyles() {
    // Write your Google Map Custom Style Code Here.
  }
  private backHome() {

    this.router.navigate(['home']);
  }
  private displayArrow(index, length) {
    console.log(length);
    this.carouselComponent.displayArrow(index, length);  //calling displayArrow func in carouselComponent to show arrow on card

  }

  private hideArrow(index) {
    console.log('leave');
    this.carouselComponent.hideArrow(index); //calling hideArrow func in carouselComponent to hide arrow on card
  }
  private changeZindex(l) {
    // var el=document.getElementById('mar-' + index);
    //el.style.zIndex='1100';
    console.log('over');
    console.log(l);
    //  this.zindex=1100;
    l.zIndex = 1100;
  }
  private changebackZindex(l) {
    console.log('leave');
    l.zIndex = 900;
  }
}




