import { Injectable } from '@angular/core';

import { Util } from './util';

// var ParseLibrary = require('parse/dist/parse.js');
import * as ParseLibrary from 'parse/dist/parse.js';

@Injectable()
export class ParseService {

  private parse;

  constructor() {
    this.parse = ParseLibrary;
    this.init();
  }

  private init() {
    this.parse.initialize(Util.PARSE_APP_ID);
    this.parse.serverURL = Util.PARSE_SERVER_URL;
  }

  sdk() {
    return this.parse;
  }

}



