import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { CreateComponent } from './create/create.component';
import { ProfileComponent } from './profile/profile.component';
import { JourneyMapComponent } from './journey-map/journey-map.component';
import { MainPanelComponent } from './main-panel/main-panel.component';
import { UserJourniesComponent } from './user-journies/user-journies.component';
import { SettingsComponent } from './settings/settings.component';
import { BroadcastComponent } from './broadcast/broadcast.component';
import { CesiumComponent } from "./cesium/cesium.component";
import { RequestsComponent } from "./requests/requests.component";
import { HelpComponent } from "./help/help.component";
import {DiscoverComponent} from "./discover/discover.component"
import {DiscoverJourneyComponent} from "./discover-journey/discover-journey.component"

export const AppRoutes: any = [
    { path: "login", component: LoginComponent },
    { path: "signup", component: SignupComponent },
    { path: 'video', component: CesiumComponent },
    { path: "discover", component: DiscoverComponent },
    { path: "discover/:id", component: DiscoverJourneyComponent },
    {
        path: "", component: MainPanelComponent,
        children: [
            { path: "home", component: HomeComponent },
            { path: 'myJournies', component: UserJourniesComponent },
            { path: 'broadcast', component: BroadcastComponent },
            { path: 'create', component: CreateComponent },
            { path: 'settings', component: SettingsComponent },
            { path: 'profile/:username/:userId', component: ProfileComponent },
            { path: 'journey/:id/:polyline', component: JourneyMapComponent },
            { path: 'requests', component: RequestsComponent },
            { path: 'help', component: HelpComponent }
        ]
    }
];

export const AppComponents: any = [
    MainPanelComponent,
    LoginComponent,
    HomeComponent,
    SignupComponent,
    ProfileComponent,
    JourneyMapComponent,
    SettingsComponent,
    CesiumComponent,
    DiscoverComponent,
    DiscoverJourneyComponent
];


