import { Component, OnInit, Pipe, PipeTransform, ViewContainerRef, Output, EventEmitter, ViewChild } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser'
import { Router } from '@angular/router';
import { AuthenticationService, User } from '../authentication.service';
import { ParseService } from '../parse.service';
//import { Overlay, overlayConfigFactory } from 'angular2-modal';
//import { Modal, BSModalContext } from 'angular2-modal/plugins/bootstrap';
// import { CustomModalContext, CreateComponent } from '../create/create.component';
import { Util } from '../util';
import { DynamicComponent } from '../dynamic/dynamic.component';
import { NotificationComponent } from '../notification/notification.component';
import { ChatComponent } from '../chat/chat.component';
import * as _ from 'underscore';

import { InboxComponent } from '../inbox/inbox.component';

let imagePath = '/assets/images/p2.png';

declare var localStorage: any;

@Component({
  selector: 'app-main-panel',
  templateUrl: './main-panel.component.html',
  styleUrls: ['./main-panel.component.css'],
  providers: [AuthenticationService, InboxComponent]
})
export class MainPanelComponent implements OnInit {

  comment: boolean = false;
  view: number = 1;
  name: string;
  listname = [];
  htmltoadd: string;
  html1: string;
  html2: string;
  html3: string;
  html4: string;
  parse;
  user_id;        //for search user
  curuser_id;
  coverImg;
  profileImg;
  postData = [];
  userarray = [];
  slideIndex = 1;
  slides;
  messages = [];
  _router: Router;
  likeButton = [];
  componentData = null;
  leftSideNavdata = [{
    text: 'Travel Feed',
    icon: 'langauge'
  },
  {
    text: 'Broadcast',
    icon: 'wifitethering'
  },
  {
    text: 'Journies',
    icon: 'flight'
  },
  {
    text: 'Help',
    icon: 'help'
  },
  {
    text: 'About',
    icon: 'flight'
  },

  ];
  @ViewChild(NotificationComponent)
  private notificationComponent: NotificationComponent;

  constructor(private _service: AuthenticationService, parse: ParseService, router: Router,// overlay: Overlay, 
    vcRef: ViewContainerRef
    //, public modal: Modal
    ) {
    this.parse = parse.sdk();
    this._router = router;
    if (!this.parse.User.current()) {
      this.gotoLogin()
    }
    else {
      // go to the url
      this.gotoPage(this._router.url);
    }

    //overlay.defaultViewContainer = vcRef;

    var currentUser = this.parse.User.current();
    var query = new this.parse.Query(this.parse.User);
    var Post = this.parse.Object.extend('Post');
    var Connection = this.parse.Object.extend('Connection');
    this.curuser_id = currentUser.id;

    query.equalTo("username", currentUser.getUsername());  // 
    query.find({
      success: user => {
        // Do stuff
        for (var i = 0; i < user.length; i++) {
          var object = user[i];
          this.name = object.get('username');                    //getting username to display the profile name in the top left
          Util.user = this.name;
          if (object.get('coverPic')){
            Util.coverImg = object.get('coverPic').url();   //getting cover image to display the image in the top left
          this.coverImg=Util.coverImg;
        }
          if (!object.get('profilePic')) {
            Util.profileImg= imagePath;
            this.profileImg = Util.profileImg;
            

          }
          else {
            Util.profileImg=object.get('profilePic').url();
          this.profileImg =Util.profileImg;
        
          }
          //console.log(object.get('cover_pic_id').url());
        }
      }
    });
  }
ngAfterViewChecked() {
  if(Util.profileImg){
//this.profileImg=Util.profileImg;

  } 
  // console.log('cg'); 
  if(Util.coverImg){

    //this.coverImg=Util.coverImg;
  }
  
  }
  gotoLogin() {
    this._router.navigate(['/login']);
  }

  gotoHome() {
    this._router.navigate(['/home']);
  }

  gotoPage(url) {
    this._router.navigate([url]);
  }

  navigateProfile(username, userid) {
    this._router.navigate(['/profile/' + username + '/' + userid]);
  }

  /**
   * it gets called multiple times, each time a character is entered, it gets called
   * So, everytime the query resolves, clear the listname
   */
  searchUser(event) {
    this.listname = [];
    if (event.target.value) {

      var query = new this.parse.Query(this.parse.User);
      //console.log(currentUser.getUsername());
      query.startsWith("username", event.target.value);  // 
      query.find({
        success: users => {
          // console.log('received: ' + users.length + ' matching names');
          this.listname = [];
          for (var i = 0; i < users.length; i++) {
            var object = users[i];
            this.user_id = object.id;
            this.listname.push({ id: this.user_id, name: object.get('username') });
            // console.log('Pushed: ', this.listname[i].name);
            
            //this.coverImg=object.get('cover_pic_id').url();
            //this.profileImg=object.get('profilePic').url();
            //console.log(object.get('cover_pic_id').url());
          }
        }
      });
    }
  }

  createPost() {
    // return this.modal.open(CreateComponent, overlayConfigFactory({ num1: 2, num2: 3 }, BSModalContext));
  }

  public openbox(chatname) {
    console.log(chatname);
    this.componentData = {
      component: ChatComponent,
      inputs: {
        showChatName: chatname
      }
    };
  }

  logout() {
    this._service.logout();
  }

  ngOnInit() {

    this.storeForVideo();

    // TODO set following
    // this.listentToRequestPushes();
  }

  /**
   * TODO improve this
   * stores currentUser's token to localStorage
   */
  storeForVideo() {
    var user = this.parse.User.current();
    let userToken = user.get("sessionToken");
    console.log("sessionToken: ", userToken);
    
    localStorage.setItem("userToken", userToken);
  }

  
  /**
   * listens for change of localStorage key "numReqs"
   */
  listentToRequestPushes() {
    // TODO improve this hack logic
    // after every 3 seconds
    setInterval(() => {
      let prevReqs = localStorage.getItem("numReqs");
      console.log('cron job for numReqs');
      console.log('prevReqs: ', prevReqs);
    }, 3000);
  }

  private showNotification() {
    console.log('show notification dropdown');
    this.notificationComponent.showNotification();
  }
  
  private travelfeed(index) {
    var l = document.getElementsByClassName('br');
    for (var i = 0; i < l.length; i++) {
      var ele = document.getElementsByClassName('br')[i] as HTMLElement;
      ele.style.borderLeftColor = '#e2e1e0';
    }
    console.log(l.length);
    var el = document.getElementsByClassName('br')[index] as HTMLElement;
    el.style.borderLeftColor = 'blue';
    this.gotoHome();
  }

  private broadCast(index) {
    var l = document.getElementsByClassName('br');
    for (var i = 0; i < l.length; i++) {
      var ele = document.getElementsByClassName('br')[i] as HTMLElement;
      ele.style.borderLeftColor = '#e2e1e0';
    }
    console.log(l.length);
    var el = document.getElementsByClassName('br')[index] as HTMLElement;
    el.style.borderLeftColor = 'blue';
      this._router.navigate(['/broadcast']);
  }

  private journies(index) {
    var l = document.getElementsByClassName('br');
    for (var i = 0; i < l.length; i++) {
      var ele = document.getElementsByClassName('br')[i] as HTMLElement;
      ele.style.borderLeftColor = '#e2e1e0';
    }
    console.log(l.length);
    var el = document.getElementsByClassName('br')[index] as HTMLElement;
    el.style.borderLeftColor = 'blue';
    this._router.navigate(['/myJournies']);
  }

  private help(index) {
    var l = document.getElementsByClassName('br');
    
    for (var i = 0; i < l.length; i++) {
      var ele = document.getElementsByClassName('br')[i] as HTMLElement;
      ele.style.borderLeftColor = '#e2e1e0';
    }

    console.log(l.length);
    var el = document.getElementsByClassName('br')[index] as HTMLElement;
    el.style.borderLeftColor = 'blue';

    this._router.navigate(['/help']);
  }

  /**
   * takes to Following Requests page
   */
  private requests(index) {
    var l = document.getElementsByClassName('br');
    for (var i = 0; i < l.length; i++) {

      var ele = document.getElementsByClassName('br')[i] as HTMLElement;
      ele.style.borderLeftColor = '#e2e1e0';

    }
    console.log(l.length);
    var el = document.getElementsByClassName('br')[index] as HTMLElement;
    el.style.borderLeftColor = 'blue';

    // go to requsts page
    this._router.navigate(['/requests']);
  }


  private settings(index) {
    var l = document.getElementsByClassName('br');
    
    for (var i = 0; i < l.length; i++) {
      var ele = document.getElementsByClassName('br')[i] as HTMLElement;
      ele.style.borderLeftColor = '#e2e1e0';
    }

    console.log(l.length);
    var el = document.getElementsByClassName('br')[index] as HTMLElement;
    el.style.borderLeftColor = 'blue';
    this._router.navigate(['/settings']);
  }
}



