import { Component, OnInit, Input, Injector, Output, EventEmitter,ViewChild,ElementRef, AfterViewChecked  } from '@angular/core';
import { DialogRef, ModalComponent, CloseGuard } from 'angular2-modal';
import { BSModalContext } from 'angular2-modal/plugins/bootstrap';

import { ParseService } from '../parse.service';
import { LiveQueryService } from '../live-query.service';
import { Util } from '../util';

export class CustomModalContext extends BSModalContext {
  public num1: number;
  public num2: number;
}
let imagePath = '/assets/images/p2.png';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css'],
  providers: [LiveQueryService]
})
export class ChatComponent implements OnInit, AfterViewChecked  {

  context: CustomModalContext;
  public hasBaseDropZoneOver: boolean = false;
  public hasAnotherDropZoneOver: boolean = false;
@ViewChild('msg') private myScrollContainer: ElementRef;
  parse;
  user;
  otherUser;
  currentChat;
  messages = [];
  showChatName;
  incoming = false;
  otherUserDP;
  userDP;
  // text of message
  text: string = "";

  isDisabled: boolean = false;
  @Output()
  closed: EventEmitter<string> = new EventEmitter();
  constructor(
    // public dialog: DialogRef<CustomModalContext>,
    parseService: ParseService,
    private liveQuery: LiveQueryService,
    private injector: Injector
  ) {
    this.parse = parseService.sdk();
    // this.context = dialog.context;
    //dialog.setCloseGuard(this);
    this.showChatName = this.injector.get('showChatName');
  }
  

  ngOnInit() {
    this.user = this.parse.User.current();
    //console.log(this.user);

    // get required fields
    this.otherUser = Util.otherChatUser;
    this.currentChat = Util.selectedChat;

    if (this.otherUser.get('profilePic'))
      this.otherUserDP = this.otherUser.get('profilePic').url();
    else this.otherUserDP = imagePath;
    if (this.user.get('profilePic'))
      this.userDP = this.user.get('profilePic').url();
    else this.userDP = imagePath;


    if (!this.otherUser) {
      console.log('Problem: otherUser not provided');
      return;
    }
    else if (!this.currentChat) {
      // this should be because the chat doesn't exist
      // fetch chat between these users
      let query = new this.parse.Query('Chat');
      query.containsAll("users", [this.user, Util.otherChatUser]);
      query.limit(1);
      query.find().then((chats) => {
        console.log('Found chat', chats);
        if (chats.length == 1) {
          this.currentChat = chats[0];
          // now fetch the messages
          this.getMessages(this.currentChat);
        }
        // else 
        // chat doesn't exist, so create new one when user sends new message
      }, (err) => {
        this.logErr(err);
      });
    }

    if (this.currentChat) {
      this.getMessages(this.currentChat);
      this.subscribeToChat(this.currentChat);
    }
   
  }
  ngAfterViewChecked() {       
        this.scrollToBottom();        
      }

  /**
   * Subscribe to chat messages where chattingId matches.
   * @param chat ParseObject of Chat class
   */
  subscribeToChat(chat) {
    let client = this.liveQuery.getLiveQueryClient();
    client.open();

    let query = new this.parse.Query('Message');
    query.equalTo("chat", this.currentChat);
    let subscription = client.subscribe(query);
    console.log('subscribed!!');

    // when new message is created.
    subscription.on('create', (message) => {
      console.log('new message: ', message.get('text'));
      this.messages.push(message);
    });
  }

  /**
   * Retrieves previous messages of this chat
   */
  getMessages(chat) {
    let query = new this.parse.Query('Message');

    query
      .equalTo("chat", chat)
      .find().then((messages) => {
        this.messages = messages;
        console.log(messages[0].get('text'));
        // scroll to bottom
      }, (error) => {
        this.logErr(error);
      });
  }

  logErr(e) {
    console.log('Err: ', e);
  }

  closeChat() {
    // unset the fields used from Util
    Util.selectedChat = null;
    Util.otherChatUser = null;
    var el = document.getElementsByClassName('chat-window')[0] as HTMLElement;
    el.style.display = 'none';
    console.log('close');
    //  this.dialog.close();
  }

  sendMessage() {
    this.disableSend(true);

    let msgText = this.text.trim();
    if (msgText === "") {
      this.disableSend(false);
      return;
    }

    if (!this.currentChat) {
      // chat doesn't already exist, create new one the call this method
      this.insertChat(msgText);
    }

    // send the message throug live query
    let Message = this.parse.Object.extend("Message");
    let m = new Message();

    m.set("chat", this.currentChat);
    m.set("sender", this.user);
    m.set("text", msgText);
    m.save(null, {
      success: (m) => {
        // now clear the global field
        this.text = "";

        // don't push it now, it will be pushed when subscriber receives it
        // this.messages.push(m);
        // TODO hide the progress circle if any

        // also update the Chat
        this.updateChat(msgText); 	// sending local var, global might have been cleared
        this.disableSend(false);
      },
      error: (m, e) => {
        console.log(e);
        this.disableSend(false);
      }
    });

    console.log(this.text);
  }

  /**
   * Updates the document in Chat collection 
   * corresponding to these users
   */
  updateChat(msgText) {
    if (this.currentChat) {
      this.currentChat.set("sender", this.user);
      this.currentChat.set("lastMessage", msgText);
      this.currentChat.save();
    }
  }

  insertChat(msgText) {
    let self = this;
    // create new chat
    let Chat = this.parse.Object.extend('Chat');
    let chat = new Chat();
    chat.set("users", [this.user, this.otherUser]);
    chat.set("sender", this.user);
    chat.set("lastMessage", msgText);
    chat.save(null, {
      success(c) {
        console.log('new chat sasved');
        self.currentChat = c;
        self.sendMessage();
      },
      error(chat, error) {
        console.log(error);
      }
    });
  }

  disableSend(what) {
    // Send button
    this.isDisabled = what;
  }


  closeChatBox() {
    //this.closed.emit('closed');
    var el = document.getElementsByClassName('chat-window')[0] as HTMLElement;
    el.style.display = 'none';
    // console.log('close');
  }
scrollToBottom(): void {
        try {
            this.myScrollContainer.nativeElement.scrollTop = this.myScrollContainer.nativeElement.scrollHeight;
        } catch(err) { }                 
    }

}


