
/**
 * Constants and Holders
 */
export class Util {

    /*
    * Related to chat
    * Must be handled carefully, each time chat is open, set these.
    * Each time chat is closed, unset these.
    */
    static selectedChat;
    static otherChatUser;
    
    static user;
    static profileImg;
    static coverImg;
    // static PARSE_SERVER_URL = 'http://localhost:1337/parse';
    static PARSE_SERVER_URL = 'http://xt1.westeurope.cloudapp.azure.com:1337/parse';
    // static PARSE_LIVE_QUERY_URL = 'ws://localhost:1337/parse/live/';
    static PARSE_LIVE_QUERY_URL = 'ws://xt1.westeurope.cloudapp.azure.com:1337/parse/live/';
    static PARSE_APP_ID = 'MY_APP_ID';

    /**
     * Logs error
     */
    public static logErr(e, from: string): void {
        console.error('Error at Place: ' + from, e);
    }

    /**
     * Constructs the sentence that appears in Notification Area
     * @param noti Parse Class Notification
     */
    public static getSentence(noti): string {
      let sentence = '';
      let actors = noti.get('actors');
      actors.forEach((actor, i) => {
        if (i < actors.length - 1) {
          sentence += actor.get('name') + ', ';
        }
        else {
          // for last item
          sentence += actor.get('name') + ' ';
        }
      });
      
      // if count is greater than actors, then also mention the difference as others
      let diff = noti.get('count') - actors.length;
      if (diff > 0) {
        sentence += 'and ' + diff + ' others';
      }
      sentence += noti.get('verb') + ' on your ' + noti.get('affectedObjectType') ; // + noti.get('meta.affectedObjectName'); 

      return sentence;
    }

}


