import { Component, OnInit } from '@angular/core';
import { ParseService } from '../parse.service';

import { Util } from "../util";

let imagePath = '/assets/images/p2.png';

@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.css']
})
export class NotificationComponent implements OnInit {

  private parse;
  private notifyData = [];

  constructor(parse: ParseService) {
    this.parse = parse.sdk();
  }

  ngOnInit() {
    this.getNotifications();
  }

  getNotifications() {
    // console.log('getting Notifications');

    // due to ACL, all Notifications that this user can access will be fetched
    var Notification = this.parse.Object.extend('Notification');
    var query = new this.parse.Query(Notification);
    query.descending('updatedAt');
    query.include('actor');
    query.find({
      success: (notifications) => {
        this.processNotifications(notifications);
      },
      error: (notifications, error) => { 
        console.error(error);
      }
    });
  }

  processNotifications(notifications) {
    // remove previous
    this.notifyData = [];

    notifications.forEach(noti => {
      var image;
      var millisec = new Date().getTime() - new Date(noti.get('updatedAt')).getTime();
      var time = this.timeConversion(millisec);

      // get first actor's profilePic
      if (noti.get('actors')[0].get('profilePic')) {
        image = noti.get('actors')[0].get('profilePic').url();
      }
      else {
        image = imagePath;
      }

      let sentence = Util.getSentence(noti);
      // console.log(sentence);
      
      // set the notification data
      this.notifyData.push({
        id: noti.id,
        actorImage: image,
        sentence: sentence,
        time:time 
      });
    });
  }

  public showNotification() {
    var el = document.getElementById('dropmenu');
    if (el.style.display == 'none') {
      el.style.display = 'block';
      
      // now also refresh the notifications
      this.getNotifications();
    }
    else {
      el.style.display = 'none';
    }
  }

  private allowFollowing(index, userId,notifi_id) {
    var currentUser = this.parse.User.current();
    console.log(userId);
    var user = new this.parse.User();
    user.id = userId;
    var Connection = this.parse.Object.extend('Connection');
    var connection = new Connection();
    var Notification = this.parse.Object.extend('Notifications');
    var notification = new Notification();
    notification.id=notifi_id;


    connection.set('from', user);
    connection.set('to', currentUser);
    connection.save({
      success: (gameScore) => {

        this.notifyData.splice(index, 1);
       notification.destroy();
        console.log('friend');
      },
      error: function (gameScore, error) {
        console.log('Failed to create new object, with error code: ', error);
      }
    });
  }
  private rejectFollowingRequest(index,notifi_id) {  //rejecting following request and delete object from notification
    var Notification = this.parse.Object.extend('Notifications');
    var notification = new Notification();
    notification.id=notifi_id;
    notification.destroy({
      success:(data)=>{
        this.notifyData.splice(index, 1);
      }
    });

  }
   timeConversion(millisec) {

        var seconds = <any>(millisec / 1000).toFixed(1);

        var minutes = <any>(millisec / (1000 * 60)).toFixed(1);

        var hours = <any>(millisec / (1000 * 60 * 60)).toFixed(1);

        var days = (millisec / (1000 * 60 * 60 * 24)).toFixed(1);

        if (seconds < 60) {
            return seconds + " Sec";
        } else if (minutes < 60) {
            return minutes + " Min";
        } else if (hours < 24) {
            return hours + " Hrs";
        } else {
            return days + " Days"
        }
    }

}



