import { Injectable } from '@angular/core';
import { ParseService } from './parse.service';
import { Router } from '@angular/router';

export class User {
  constructor(
    public email: string,
    public username: string,
    public password: string) { }
}

var users = [
  new User('admin@admin.com', 'admin', 'admin'),
  new User('user1@gmail.com', 'asd', 'a23')
];

@Injectable()
export class AuthenticationService {

  parse;

  constructor(parse: ParseService, private _router: Router) {
    this.parse = parse.sdk();
  }

  logout() {
    // localStorage.removeItem("user");

    this.parse.User.logOut();
    this._router.navigate(['/login']);
  }

  /**
   * user: ParseUser
   * callback: Callback Method that will be called when result of login is received from ParseServer.
   */
  login(user, callback) {

    var authenticatedUser = users.find(u => u.email === user.email && u.password === user.password);

    console.log(user.username);
    console.log(user.password);

    this.parse.User.logIn(user.username, user.password, {
      success: (user) => {
        // Do stuff after successful login.
        console.log('Login successful');

        callback(true);

        this._router.navigate(['home']);
      },
      error: (user, error) => {
        callback(false);
        console.error('Failed to login. ', error);
      }
    });
  }

  checkCredentials() {
    if (localStorage.getItem("user") === null) {
      this._router.navigate(['Login']);
    }
  }

}



