import { Component, OnInit, Pipe, PipeTransform, ViewContainerRef, Output, EventEmitter, ViewChild } from '@angular/core';

import { Router } from '@angular/router';
import { AuthenticationService, User } from '../authentication.service'
import { ParseService } from '../parse.service';
import { SlideComponent } from '../slide/slide.component';
import { CarouselComponent } from '../carousel/carousel.component';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { Util } from '../util';
import * as _ from 'underscore';
let imagePath = '/assets/images/p2.png';

@Component({
  selector: 'app-user-journies',
  templateUrl: './user-journies.component.html',
  styleUrls: ['./user-journies.component.css']
})
export class UserJourniesComponent implements OnInit {
  name: string;
  listname = [];

  parse;
  user_id;        //for search user
  curuser_id;
  profileImg;
  journeyData = [];
  userarray = [];
  slideIndex = 1;
  slides;
  messages = [];
  _router: Router;
  likeButton = [];
  componentData = null;

  @ViewChild(CarouselComponent)
  private carouselComponent: CarouselComponent;

  constructor(private _service: AuthenticationService, parse: ParseService, router: Router, vcRef: ViewContainerRef,
    overlay: Overlay) {
    this._router = router;
    overlay.defaultViewContainer = vcRef;
    this.parse = parse.sdk();

    var currentUser = this.parse.User.current();
    var query = new this.parse.Query(this.parse.User);
    var Journey = this.parse.Object.extend('Journeys');

    this.name = Util.user;
    this.profileImg = Util.profileImg;

    this.curuser_id = currentUser.id;
    console.log(currentUser.getUsername());
    var query = new this.parse.Query(Journey);
    query.equalTo("createdBy", currentUser);
    query.descending('createdAt');
    query.find(
      journey => {
        for (var j = 0; j < journey.length; j++) {
          var objt = journey[j];
          var videoUrl;
          var length;
          var dis = Math.floor(objt.get('distance'));
          if (objt.get('videoUrl')) {
            videoUrl = objt.get('videoUrl');
            length = 2;
          }
          else { videoUrl = false; length = 1; }
          var ms = new Date().getTime() - new Date(objt.get('createdAt')).getTime();
          var time = this.timeConversion(ms);
          var millisec = new Date(objt.get('endDate')).getTime() - new Date(objt.get('startDate')).getTime();
          var timejourneytake = this.timeConversion(millisec);
          this.journeyData.push({
                              id: objt.id, description: objt.get('description'),posttime:time,
                              polyline: objt.get('polylines'), numMedia: objt.get('numOfMedia'),
                              distance: dis, title: objt.get('name'), view: objt.get('views'),numLikes:objt.get('likesCounter'),
                              video: videoUrl, length: length,startLocation:objt.get('startLocation'),
                              endLocation:objt.get('endLocation'),time:timejourneytake
          });
        }
        console.log(this.journeyData);
      }
    );
    var user_name;
    var user_Profimg;
  }

  ngOnInit() {
  }
 ngAfterViewChecked() {

    this.checkOverflowContent();
  }


  showComments(index, trackBy) {
    var e = document.getElementsByClassName('comment_box')[index] as HTMLElement;

    console.log('comment');
    console.log(Util.profileImg);
    this.profileImg = Util.profileImg;
    var messages1 = [];


    var Journey = this.parse.Object.extend('Journeys');
    var journey = new Journey();
    journey.id = trackBy;
    var Comment = this.parse.Object.extend('CommentJourney');
    var query = new this.parse.Query(Comment);
    query.equalTo("journey", journey);

    if (e.style.display === "none") {
      e.style.display = "block";
      var u_id;
      // 
      query.ascending('createdAt');
      query.include('user');
      query.find().then(
        comment => {
          // Do stuff
          var promise = this.parse.Promise.as();
          _.each(comment, (resultObj) => {

            promise = promise.then(abc => {
              var ob = <any>resultObj;

              console.log(ob.get('text'));
              if (!ob.get('user').get('profilePic')) {
                messages1.push({ from: ob.get('user').get('username'), image: imagePath, message: ob.get('text') });
                console.log(this.messages);
              }
              else {
                messages1.push({ from: ob.get('user').get('username'), image: ob.get('user').get('profilePic').url(), message: ob.get('text') });
              }
            });
          });
          return promise;
        });
      this.messages[index] = messages1;

    }
    else { e.style.display = 'none'; }
  }

  saveComment(event, post_id, index, type) {             //save comment
    if (event.keyCode == 13) {      //when enter key is pressed
      var image = Util.profileImg;
      var currentUser = this.parse.User.current();


      var Journey = this.parse.Object.extend('Journeys');
      var journey = new Journey();
      journey.id = post_id;

      var Comment = this.parse.Object.extend('CommentJourney');
      var comment = new Comment();
      comment.set('user', currentUser);
      comment.set('journey', journey);
      comment.set('text', event.target.value);
      // for now
      comment.set('type', 'plain_comment');
      comment.save({
        success: (comment) => {


          this.messages[index].push({ from: this.name, image: image, message: comment.get('text') });
          // this.notifyComment(post);
          console.log(comment);
        },
        error: function (comment, error) {
          console.error(error);
        }
      });
      console.log(post_id);
      console.log(this.curuser_id);
      event.target.value = "";

    }

  }

  private identity(index, item) {         //set  object id to each post
    //  console.log(index);
    // console.log(item.id);
    return item.id;
  }

  private displayArrow(index, length) {
    console.log(index);
    this.carouselComponent.displayArrow(index, length);  //calling displayArrow func in carouselComponent to show arrow on card
  }

  private hideArrow(index) {
    console.log('leave');
    this.carouselComponent.hideArrow(index); //calling hideArrow func in carouselComponent to hide arrow on card
  }

  private playPauseVideo(index) {
    console.log('play');
    var el = document.getElementsByClassName('video')[index] as HTMLVideoElement;
    var em = document.getElementsByClassName('playpause')[index] as HTMLElement;
    if (el.paused) {
      console.log('play2');
      el.play()
      em.style.opacity = '0';
    }
    else {
      el.pause();
      em.style.opacity = '1';
    }
  }

  public showJourney(id, polyline) {
    console.log(polyline[0]);
    this._router.navigate(['journey/' + id + '/' + polyline[0]]);
  }

  public incrementView(index, id, type) {
    if (type == 'post') {
      console.log('view incr');
      var Post = this.parse.Object.extend('Post');
      var post = new Post();
      post.id = id;
      post.increment('views');
      post.save();
    }
    else {
      /*  var Journey = this.parse.Object.extend('JourneyTest');
        var journeys = new Journey();
        journeys.id = id;
        journeys.increment('views');
        journeys.save();*/
    }
  }
  private checkOverflowContent() {

    for (var i = 0; i < this.journeyData.length; i++) {

      var el = document.getElementById('contentOverflow-' + i);
      var em = document.getElementById('viewmore-' + i);
      if (el && em) {

        if (el.clientHeight < el.scrollHeight) {

          em.style.display = 'block';

        }
        else {
          el.style.height = '100%';
        }

      }
    }

  }
  timeConversion(millisec) {

    var seconds = <any>(millisec / 1000).toFixed();

    var minutes = <any>(millisec / (1000 * 60)).toFixed();

    var hours = <any>(millisec / (1000 * 60 * 60)).toFixed();

    var days = <any>(millisec / (1000 * 60 * 60 * 24)).toFixed();

    if (seconds < 60) {
      return seconds + " Sec";
    } else if (minutes < 60) {
      return minutes + " Min";
    } else if (hours < 24) {
      return hours + " Hrs";
    } else {
      return days + " Days"
    }
  }

}



